import React from 'react'
import Plot from 'react-plotly.js'
import BigNumber from 'bignumber.js'
import moment from 'moment'

const MyPlot = ({ data: { result: data }, suffix }) => {
    // <DATE>,<TIME>,<OPEN>,<HIGH>,<LOW>,<CLOSE>,<VOL>
    const x = data.map(i => moment(i['<DATE>'], 'DD/MM/YY').format('YYYY-MM-DD'))
    const open = data.map(i => BigNumber(i['<OPEN>']).toNumber())
    const close = data.map(i => BigNumber(i['<CLOSE>']).toNumber())
    const high = data.map(i => BigNumber(i['<HIGH>']).toNumber())
    const low = data.map(i => BigNumber(i['<LOW>']).toNumber())
    const trace = {
        x,
        open,
        close,
        high,
        low,
        type: 'candlestick',
        xaxis: 'x',
        yaxis: 'y',
    }

    return (
        <Plot
            data={[
                trace,
            ]}
            layout={{
                yaxis: {
                    ticksuffix: ` ${suffix.toUpperCase()}`,
                },
                xaxis: {
                    type: 'date',
                    range: x,
                    // rangeslider: { range: x },
                    rangeslider: {
                        visible: false,
                    },
                    tickformat: '%d %b\n %Y',
                },
            }}
            useResizeHandler
            style={{ width: '100%', height: '80vh' }}
        />
    )
}

export default MyPlot
